* {
    color: #fff;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #282a36;
    width: 100%;
    height: 100%;
    overflow: hidden;
    color: #ffffff;
}

html,
#root,
.App {
    width: 100%;
    height: 100%;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
    text-align: center;
}

.slider {
    position: absolute;
    bottom: 10px;
    left: 10px;
    right: 10px;
}

.metadata {
    position: absolute;
    bottom: 35px;
    right: 10px;
    color: gray;
}

img {
    position: absolute;
    display: block;
    margin-left: auto;
    margin-right: auto;
    left: 0;
    right: 0;
}

.imagecontainer {
    margin: 0 auto;
    display: inline-block;
}

.button-controls {
    opacity: 0.3;
    transition: opacity 0.3s;
    padding: 25px;
    margin-top: -25px;
}

.button-controls:hover {
    opacity: 1;
    transition: opacity 0.3s;
}

.logo {
    position: absolute;
    top: 3em;
    left: 3em;
    opacity: 0.3;
    padding: 20px;
}

.logo img {
    width: 350px;
}

.logo:hover {
    opacity: 1;
    transition: opacity 0.3s;
}